@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Ubuntu&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@600&display=swap');

*{
  margin: 0;
  padding: 0;
}

/*font-family: 'Montserrat', sans-serif;
font-family: 'Ubuntu', sans-serif;
font-family: 'Cinzel', serif; */

/* //navbar css */


.navbar {
  font-family: "Montserrat", sans-serif;
  font-weight: bolder;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  background-color: #F8F0DF;
  /*box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;  */
}

.nav-link {
  font-weight: 300 !important;
  font-size: 13px;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 1000;
  font-size: 30px;
  font-family: "Ubuntu", sans-serif;
  letter-spacing: 3px;
}

.search-form {
  margin-right: 10px;
  margin-top: 4px;
}
.search-form input {
  border: none;
  border-radius: 0;
}
.search-form button {
  background-color: #000000;
  border-radius: 0;
  color: white;
}

/* =============================
==========footer============= */

.footer {
  color: white;
  padding: 40px;
  background-color: #E97777;
  text-align: center;
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #92B4EC;
  border-bottom: 1px solid #92B4EC;
}



/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
