.container-fluid{
  background-color: #FEFBF3;
}
.slide{
  padding: 0px 0px;
  margin-top: 0;
}

#slider {
  background-color: #FECD70;
  text-align: center;
  padding:0px 0px 0px 0px;
}

.first{
background-color: #FECD70;
}
.second{
  background-color: #FF9F9F;
}
.testimonial-image {
  /*border-radius: 100%;*/
  width: 40%;
  padding: 10px 0 20px 0;
}

.carousel-item {
  padding: 150px 70px 50px 90px;
  text-align: center;
}
.head{
  line-height: 2;
  font-family: 'Cinzel', serif;
  font-weight: bolder;
}
.title-image {
  width: 45%;
  transform: rotate(21deg);
  right: 20%;
}
.sec-image{
  margin-top: 0px;
  transform: rotate(21deg);
}

.home-page h1 {
  color: black;
  font-family: "Montserrat", sans-serif;
  font-weight: bolder;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}

.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}

.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}

.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}

/* ==================
filters
============== */
.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}

.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
}

.loadmore {
  color: gray;
  font-weight: bold;
  font-size: 20px !important;
}
